import { Component } from 'react';
import localStorage from 'local-storage';
import { ThemeProvider } from 'mineral-ui/themes';
import Select from 'mineral-ui/Select';
import Player from '../components/Player';

export default class Index extends Component {
  state = {
    playerCount: false,
    currentPlayer: 0,
  };

  possiblePlayers = [
    {
      text: '2',
      value: '2',
    },
    {
      text: '3',
      value: '3',
    },
    {
      text: '4',
      value: '4',
    },
  ];

  handleChange = ({ value }) => {
    const playerCount = Number(value);
    this.setState({ playerCount: playerCount });
    localStorage.setItem('playerCount', playerCount );
  };

  hydrateStateWithLocalStorage = () => {
    const { state } = this;
    Object.keys(state).forEach((key) => {
      if (! localStorage.hasOwnProperty(key)) { 
        return;
      }

      let value = localStorage.getItem(key);
      try {
        value = JSON.parse(value);
        this.setState({ [key]: value });
      } catch (e) {
        // Handle empty string
         this.setState({ [key]: value });
      }

    })
  };

  componentDidMount() {
    this.hydrateStateWithLocalStorage();
  }

  render() {
    const { possiblePlayers, handleChange } = this;
    const { playerCount } = this.state;

    return (
      <ThemeProvider>
        <div>
          {!playerCount && (
            <div>
              <p>How many players are there?</p>
              <Select
                data={this.possiblePlayers}
                defaultSelectedItem={possiblePlayers[0]}
                required
                onChange={handleChange}
              />
            </div>
          )}
          <h1>Todo</h1>
          <ul>
            <li>Make the player grid</li>
            <li>Make a component to hold the tiles</li>
            <li>Make a component to hold the mats</li>
          </ul>
          <Player />
        </div>
      </ThemeProvider>
    );
  }
}
